.mat-tab-header {
  display: flex;
  overflow: hidden;
  position: relative;
  flex-shrink: 0; }

.mat-tab-label {
  line-height: 48px;
  height: 48px;
  padding: 0 12px;
  font-size: 14px;
  font-family: Roboto, "Helvetica Neue", sans-serif;
  font-weight: 500;
  cursor: pointer;
  box-sizing: border-box;
  color: currentColor;
  opacity: 0.6;
  min-width: 160px;
  text-align: center;
  position: relative; }
  .mat-tab-label:focus {
    outline: none;
    opacity: 1; }

@media (max-width: 600px) {
  .mat-tab-label {
    min-width: 72px; } }

.mat-ink-bar {
  position: absolute;
  bottom: 0;
  height: 2px;
  transition: 500ms cubic-bezier(0.35, 0, 0.25, 1); }
  .mat-tab-group-inverted-header .mat-ink-bar {
    bottom: auto;
    top: 0; }

.mat-tab-header-pagination {
  position: relative;
  display: none;
  justify-content: center;
  align-items: center;
  min-width: 32px;
  cursor: pointer;
  z-index: 2; }
  .mat-tab-header-pagination-controls-enabled .mat-tab-header-pagination {
    display: flex; }

.mat-tab-header-pagination-before, .mat-tab-header-rtl .mat-tab-header-pagination-after {
  padding-left: 4px; }
  .mat-tab-header-pagination-before .mat-tab-header-pagination-chevron, .mat-tab-header-rtl .mat-tab-header-pagination-after .mat-tab-header-pagination-chevron {
    transform: rotate(-135deg); }

.mat-tab-header-rtl .mat-tab-header-pagination-before, .mat-tab-header-pagination-after {
  padding-right: 4px; }
  .mat-tab-header-rtl .mat-tab-header-pagination-before .mat-tab-header-pagination-chevron, .mat-tab-header-pagination-after .mat-tab-header-pagination-chevron {
    transform: rotate(45deg); }

.mat-tab-header-pagination-chevron {
  border-style: solid;
  border-width: 2px 2px 0 0;
  content: '';
  height: 8px;
  width: 8px; }

.mat-tab-header-pagination-disabled {
  box-shadow: none;
  cursor: default; }
  .mat-tab-header-pagination-disabled .mat-tab-header-pagination-chevron {
    border-color: #ccc; }

.mat-tab-label-container {
  display: flex;
  flex-grow: 1;
  overflow: hidden;
  z-index: 1; }

.mat-tab-list {
  flex-grow: 1;
  position: relative;
  transition: transform 500ms cubic-bezier(0.35, 0, 0.25, 1); }

.mat-tab-labels {
  display: flex; }
